"use client"

import { DashboardLayout } from "@/components/dashboard-layout"
import { useAuth } from "@/components/auth-provider"
import { AdminDashboard } from "@/components/dashboard/admin-dashboard"
import { HodDashboard } from "@/components/dashboard/hod-dashboard"
import { FacultyDashboard } from "@/components/dashboard/faculty-dashboard"
import { StudentDashboard } from "@/components/dashboard/student-dashboard"
import { ChangePassword } from "@/components/dashboard/change-password" // Added ChangePassword component import
import {
  LayoutDashboard,
  Users,
  FileCheck,
  Calendar,
  FileText,
  BookOpen,
  Target,
  GraduationCap,
  ClipboardList,
  Settings,
} from "lucide-react"
import { useState, type ReactNode } from "react" // Added state for navigation

type NavItem = {
  id: string
  label: string
  icon: ReactNode
}

export default function DashboardPage() {
  const { user } = useAuth()
  const [activeTab, setActiveTab] = useState("dashboard") // Handle dashboard views with state instead of URLs

  const getNavigation = (): NavItem[] => {
    const baseNav: NavItem[] = [{ id: "dashboard", label: "Dashboard", icon: <LayoutDashboard className="w-4 h-4" /> }]

    let roleNav: NavItem[] = []
    if (user) {
      switch (user.role) {
        case "admin":
          roleNav = [
            { id: "users", label: "Users", icon: <Users className="w-4 h-4" /> },
            { id: "requests", label: "Requests", icon: <FileCheck className="w-4 h-4" /> },
            { id: "reports", label: "Reports", icon: <FileText className="w-4 h-4" /> },
          ]
          break
        case "hod":
          roleNav = [
            { id: "mentor-assignments", label: "Mentor Assignments", icon: <Users className="w-4 h-4" /> },
            { id: "faculty", label: "Faculty", icon: <Users className="w-4 h-4" /> },
            { id: "students", label: "Students", icon: <GraduationCap className="w-4 h-4" /> },
            { id: "approvals", label: "Approvals", icon: <FileCheck className="w-4 h-4" /> },
          ]
          break
        case "faculty":
          roleNav = [
            { id: "my-students", label: "My Students", icon: <Users className="w-4 h-4" /> },
            { id: "meetings", label: "Meetings", icon: <Calendar className="w-4 h-4" /> },
            { id: "requests", label: "Requests", icon: <ClipboardList className="w-4 h-4" /> },
          ]
          break
        case "student":
          roleNav = [
            { id: "mentor", label: "My Faculty", icon: <Users className="w-4 h-4" /> },
            { id: "meetings", label: "Meetings", icon: <Calendar className="w-4 h-4" /> },
            { id: "academics", label: "Academics", icon: <BookOpen className="w-4 h-4" /> },
            { id: "goals", label: "Goals", icon: <Target className="w-4 h-4" /> },
          ]
          break
      }
    }

    return [
      ...baseNav,
      ...roleNav,
      { id: "settings", label: "Change Password", icon: <Settings className="w-4 h-4" /> },
    ]
  }

  const renderContent = () => {
    if (activeTab === "settings") {
      return <ChangePassword />
    }

    switch (user?.role) {
      case "admin":
        return <AdminDashboard />
      case "hod":
        return <HodDashboard />
      case "faculty":
        return <FacultyDashboard />
      case "student":
        return <StudentDashboard />
      default:
        return null
    }
  }

  return (
    <DashboardLayout
      navigation={getNavigation()}
      activeTab={activeTab}
      onTabChange={setActiveTab} // Pass state controls to layout
    >
      {renderContent()}
    </DashboardLayout>
  )
}
