"use client"

import type React from "react"

import { useEffect } from "react"
import { useRouter } from "next/navigation"
import { useAuth } from "./auth-provider"
import { Button } from "./ui/button"
import { GraduationCap, LogOut, Menu } from "lucide-react"
import { logout } from "@/lib/auth"
import { useState } from "react"
import { Sheet, SheetContent, SheetTrigger } from "./ui/sheet"
import type { UserRole } from "@/lib/types"

type DashboardLayoutProps = {
  children: React.ReactNode
  navigation: {
    id: string // Added ID for active state tracking
    label: string
    href?: string
    icon: React.ReactNode
  }[]
  requiredRole?: UserRole
  activeTab?: string // Added activeTab
  onTabChange?: (id: string) => void // Added onTabChange callback
}

export function DashboardLayout({ children, navigation, requiredRole, activeTab, onTabChange }: DashboardLayoutProps) {
  const router = useRouter()
  const { user, loading, setUser } = useAuth()
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false)

  useEffect(() => {
    if (!loading) {
      if (!user) {
        router.push("/login")
      }
    }
  }, [user, loading, router])

  const handleLogout = () => {
    logout()
    setUser(null)
    router.push("/login")
  }

  if (loading || !user) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary" />
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
        <div className="flex h-16 items-center justify-between px-4 md:px-6">
          <div className="flex items-center gap-4">
            <Sheet open={mobileMenuOpen} onOpenChange={setMobileMenuOpen}>
              <SheetTrigger asChild className="lg:hidden">
                <Button variant="ghost" size="icon">
                  <Menu className="w-5 h-5" />
                </Button>
              </SheetTrigger>
              <SheetContent side="left" className="w-64 p-0">
                <div className="flex flex-col h-full">
                  <div className="p-6 border-b">
                    <div className="flex items-center gap-2">
                      <div className="w-8 h-8 bg-primary rounded-lg flex items-center justify-center">
                        <GraduationCap className="w-5 h-5 text-primary-foreground" />
                      </div>
                      <span className="font-semibold text-lg">MentorHub</span>
                    </div>
                  </div>
                  <nav className="flex-1 p-4">
                    <ul className="space-y-1">
                      {navigation.map((item) => (
                        <li key={item.id}>
                          <button // Use buttons instead of anchors for single-url routing
                            onClick={() => {
                              onTabChange?.(item.id)
                              setMobileMenuOpen(false)
                            }}
                            className={`w-full flex items-center gap-3 px-3 py-2 text-sm font-medium rounded-lg transition-colors ${
                              activeTab === item.id ? "bg-primary text-primary-foreground" : "hover:bg-accent"
                            }`}
                          >
                            {item.icon}
                            {item.label}
                          </button>
                        </li>
                      ))}
                    </ul>
                  </nav>
                </div>
              </SheetContent>
            </Sheet>

            <div className="flex items-center gap-2">
              <div className="w-8 h-8 bg-primary rounded-lg flex items-center justify-center">
                <GraduationCap className="w-5 h-5 text-primary-foreground" />
              </div>
              <span className="font-semibold text-lg hidden sm:inline">MentorHub</span>
            </div>
          </div>

          <div className="flex items-center gap-4">
            <div className="hidden md:flex flex-col items-end">
              <span className="text-sm font-medium">{user.name}</span>
              <span className="text-xs text-muted-foreground capitalize">{user.role}</span>
            </div>
            <Button variant="ghost" size="sm" onClick={handleLogout}>
              <LogOut className="w-4 h-4 mr-2" />
              <span className="hidden sm:inline">Logout</span>
            </Button>
          </div>
        </div>
      </header>

      <div className="flex">
        {/* Sidebar - Desktop */}
        <aside className="hidden lg:block w-64 border-r min-h-[calc(100vh-4rem)] bg-background">
          <nav className="p-4">
            <ul className="space-y-1">
              {navigation.map((item) => (
                <li key={item.id}>
                  <button // Changed to button for in-page navigation
                    onClick={() => onTabChange?.(item.id)}
                    className={`w-full flex items-center gap-3 px-3 py-2 text-sm font-medium rounded-lg transition-colors ${
                      activeTab === item.id ? "bg-primary text-primary-foreground" : "hover:bg-accent"
                    }`}
                  >
                    {item.icon}
                    {item.label}
                  </button>
                </li>
              ))}
            </ul>
          </nav>
        </aside>

        {/* Main Content */}
        <main className="flex-1 p-4 md:p-6 lg:p-8">{children}</main>
      </div>
    </div>
  )
}
